@keyframes preloader-animation {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}
/*
// base
*/
html {
  height: 100%;
}
body {
  height: 100%;
  font-family: "Roboto", "sans-serif";
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  background: #fff;
  color: #3d464d;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  overflow-y: scroll;
}
[dir=ltr] body {
  text-align: left;
  direction: ltr;
}
[dir=rtl] body {
  text-align: right;
  direction: rtl;
}
svg {
  display: block;
}
a {
  color: #1a66ff;
}
a:hover {
  color: #1a66ff;
  text-decoration: none;
}
label {
  margin-bottom: 0.25rem;
}
h1 {
  font-size: 36px;
  font-weight: 700;
}
h2 {
  font-size: 32px;
  font-weight: 700;
}
h3 {
  font-size: 28px;
  font-weight: 700;
}
h4 {
  font-size: 24px;
  font-weight: 700;
}
h5 {
  font-size: 20px;
  font-weight: 700;
}
h6 {
  font-size: 16px;
  font-weight: 700;
}
/*
// .document
*/
.document {
  border: 2px solid #f0f0f0;
  border-radius: 2px;
  padding: 85px 200px 120px;
}
.document__header {
  text-align: center;
  margin-bottom: 80px;
}
.document__subtitle {
  margin-top: 12px;
  font-size: 14px;
  line-height: 22px;
  color: #999;
}
@media (max-width: 1199px) {
  .document {
    padding: 75px 110px 110px;
  }

  .document__header {
    margin-bottom: 60px;
  }
}
@media (max-width: 991px) {
  .document {
    padding: 45px 35px 60px;
  }

  .document__header {
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .document {
    padding: 35px 30px 50px;
  }

  .document__header {
    margin-bottom: 30px;
  }

  .document__title {
    font-size: 30px;
    margin-bottom: 0;
  }

  .document__subtitle {
    margin-top: 6px;
  }
}
@media (max-width: 575px) {
  .document {
    padding: 26px 20px 40px;
  }

  [dir=ltr] .document__header {
    text-align: left;
  }
  [dir=rtl] .document__header {
    text-align: right;
  }
}
/*
// .fake-svg-icon
*/
/* this needs only to avoid slow performance on Microsoft Edge while rendering huge amount of svg*/
.fake-svg-icon {
  font-family: "distriland" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: none;
  vertical-align: top;
}
.fake-svg-icon--wishlist-16 {
  width: 16px;
  height: 16px;
  font-size: 16px;
}
.fake-svg-icon--wishlist-16::before {
  content: "";
}
.fake-svg-icon--compare-16 {
  width: 16px;
  height: 16px;
  font-size: 16px;
}
.fake-svg-icon--compare-16::before {
  content: "";
}
/*
// .page-header
*/
.page-header__container {
  padding-bottom: 32px;
}
.page-header__breadcrumb {
  padding-top: 16px;
}
.page-header__title {
  padding-top: 6px;
}
.page-header__title h1 {
  margin-bottom: 0;
}
@media (min-width: 768px) and (max-width: 991px) {
  .page-header__title {
    padding-top: 4px;
  }
  .page-header__title h1 {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .page-header__container {
    padding-bottom: 24px;
  }

  .page-header__title {
    padding-top: 2px;
  }
  .page-header__title h1 {
    margin-bottom: 0;
    font-size: 30px;
  }
}
/*
// .site
*/
.site {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
.site__header {
  flex-shrink: 0;
}
.site__body {
  flex-grow: 1;
}
.site__footer {
  flex-shrink: 0;
}
/* ----------------------------------------
// Components
// ---------------------------------------- */
/*
// .account-nav
*/
.account-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.account-nav__item a {
  display: block;
  color: #6c757d;
  font-size: 15px;
}
.account-nav__item a:hover {
  background-color: #f7f7f7;
}
@media (min-width: 992px) {
  .account-nav {
    border: 2px solid #f0f0f0;
    border-radius: 2px;
  }
  .account-nav ul {
    padding-bottom: 18px;
  }

  .account-nav__title {
    font-size: 20px;
    padding: 24px 26px 0;
    margin-bottom: 1rem;
  }

  .account-nav__item a {
    padding: 5px 26px;
  }
  [dir=ltr] .account-nav__item a {
    margin-left: -2px;
    border-left: 2px solid #f0f0f0;
  }
  [dir=rtl] .account-nav__item a {
    margin-right: -2px;
    border-right: 2px solid #f0f0f0;
  }

  .account-nav__item--active a {
    color: #3d464d;
    font-weight: 500;
  }
  [dir=ltr] .account-nav__item--active a {
    border-left: 2px solid #ffd333;
  }
  [dir=rtl] .account-nav__item--active a {
    border-right: 2px solid #ffd333;
  }
}
@media (max-width: 991px) {
  .account-nav__title {
    display: none;
  }

  .account-nav {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  .account-nav ul {
    display: flex;
    flex-wrap: nowrap;
    white-space: nowrap;
    width: 100%;
    border-bottom: 2px solid #f0f0f0;
  }

  .account-nav__item a {
    padding: 8px 16px;
    margin-bottom: -2px;
    border-bottom: 2px solid #f0f0f0;
  }

  .account-nav__item--active a {
    color: #3d464d;
    font-weight: 500;
    border-color: #ffd333;
  }
}
/*
// .alert
*/
.alert {
  border: none;
  border-radius: 2px;
  font-size: 15px;
  line-height: 20px;
  padding: 0.75rem 1rem;
}
.alert a {
  box-decoration-break: clone;
  -webkit-box-decoration-break: clone;
  color: currentColor;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 1.5px;
  padding: 0 3px;
}
.alert a:hover {
  background: rgba(255, 255, 255, 0.85);
}
.alert a:active {
  background: rgba(255, 255, 255, 0.5);
}
.alert-primary {
  background: #b2d9ff;
  color: #36414d;
}
.alert-secondary {
  background: #d9d9d9;
  color: #4d4d4d;
}
.alert-info {
  background: #b8e2e5;
  color: #2d3e40;
}
.alert-success {
  background: #bde5a1;
  color: #34402d;
}
.alert-danger {
  background: #ffb8b8;
  color: #473333;
}
.alert-warning {
  background: #f2e291;
  color: #4d472e;
}
.alert-lg {
  font-size: 16px;
  padding: 1.125rem 1.375rem;
}
[dir=ltr] .alert-dismissible {
  padding-right: 3rem;
}
[dir=rtl] .alert-dismissible {
  padding-left: 3rem;
}
.alert-dismissible .close {
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  height: 100%;
  transition: all 0.2s;
  fill: rgba(0, 0, 0, 0.3);
  background: transparent;
}
[dir=ltr] .alert-dismissible .close {
  left: auto;
  right: 0;
  border-top-left-radius: 0;
  border-top-right-radius: 1.5px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 1.5px;
}
[dir=rtl] .alert-dismissible .close {
  right: auto;
  left: 0;
  border-top-right-radius: 0;
  border-top-left-radius: 1.5px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 1.5px;
}
.alert-dismissible .close:focus {
  outline: none;
}
.alert-dismissible .close:not(:disabled):not(.disabled):focus,
.alert-dismissible .close:not(:disabled):not(.disabled):hover {
  background: rgba(0, 0, 0, 0.08);
  fill: rgba(0, 0, 0, 0.5);
  opacity: 1;
}
/*
// .badge
*/
.badge {
  border-radius: 1.5px;
  font-weight: 500;
  padding: 0.25em 0.5em;
}
/*
// .block-header
*/
.block-header {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}
.block-header__title {
  margin-bottom: 0;
  font-size: 20px;
}
