  text-transform: uppercase;
  fill: currentColor;
  padding: 7px;
  cursor: pointer;
  top: 0;
  background: transparent;
  color: #ccc;
  transition: background-color 0.2s, color 0.2s;
  /* this needs only to avoid slow performance on Microsoft Edge while rendering huge amount of svg */
}
